.folderTabWrapper {
  min-width: 250px;
}
.mobileFolderTabWrapper {
}
.mobileFolderTab {
  background: #191919;
  border: solid 1px #2a2a2a;
  height: 100vh;
  min-width: 80vw;
  padding: 20px 0px;
}
.folderTab {
  position: fixed;
  top: 0px;
  left: 60px;

  background: #191919;
  border: solid 1px #2a2a2a;
  height: 100vh;
  width: 250px;
  padding: 20px 0px;
}

.sectionTitle {
  color: white;
  font-weight: bold;
  padding-left: 23px;
  font-size: 25px;
  padding-bottom: 20px;
  margin-bottom: 5px;
}

.keyTitleWrapper {
  border-left: solid 3px #2074ec;
  padding-left: 20px;
  color: white;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
}
.titleWrapper {
  padding-bottom: 10px;
  padding-left: 24px;
  color: #b2b2b2;
  display: flex;
  align-items: center;
}
.keyTitle {
  font-size: 16px;
  padding-left: 16px;
  font-weight: bold;
}
.subscriptionWrapper {
  display: flex;
  align-items: flex-start;
  padding-bottom: 5px;
  padding-top: 10px;
}
.subscription {
  font-size: 14px;
  color: #b2b2b2;
  padding-right: 95px;
  padding-left: 25px;
  font-weight: 500;
}
.tagWrapper {
  display: flex;
  align-items: flex-start;
  padding-bottom: 5px;
  padding-top: 15px;
}
.tag {
  font-size: 14px;
  color: #b2b2b2;
  padding-right: 160px;
  padding-left: 25px;
  font-weight: 500;
}
.importWrapper {
  width: 94%;
  margin-left: 0.5rem;
  background-color: #164cdf;
  border-radius: 20px;
  padding-left: 0.9rem;

  padding-top: 0.5rem;

  padding-bottom: 0.5rem;
  margin-bottom: 1rem;

  display: flex;
  align-items: center;
}

.importTitle {
  color: white;
  font-size: 16px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: bold;
  padding-left: 16px;
}
.labelWrapper {
  display: flex;
  align-items: center;
}
.labelTitle {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-weight: 600;
  font-size: 14px;

  padding-left: 5px;
}
.subTitle {
  padding-left: 30px;
  font-weight: bold;
  padding-bottom: 5px;
  color: #b2b2b2;
}
.setting {
  cursor: pointer;
  padding-right: 4px;
}

.popup {
  display: flex;
  position: fixed;
  left: 0;
  top: 0;
  color: #e2e2e2e2;
  z-index: 99;
  animation: popUpSlide 0.4s;
  height: 100vh;
  width: 100%;
}

.blur {
  min-width: 20%;
  background-color: #131313;
  opacity: 90%;
}

.addFolder {
  padding-top: 2.7rem;
  padding-left: 7.8rem;
  background-color: #131313;
  width: 80%;
}

.addTitle {
  color: #e0e0e0;
  font-weight: bold;
  font-size: 30px;
  padding-bottom: 10px;
}

.addSubTitle {
  font-weight: bold;
  padding-bottom: 5px;
  color: #b2b2b2;
  padding-bottom: 2rem;
}

.input {
  width: 30%;
  padding-top: 7px;
  padding-bottom: 7px;
  border-radius: 5px;
  height: 30px;
  background-color: black;
  color: #6c6c6c;
  padding-left: 20px;
  font-size: 17px;
  border: solid 1px white;
}
.saveBtn {
  padding: 0 8px;
  width: 60px;
  margin-left: 10px;
  font-size: 13px;
  font-weight: bold;
  height: 30px;
  border-radius: 5px;
  border: none;
  color: #e0e0e0;
  background-color: #01b468;
  cursor: pointer;
}
.cancelBtn {
  padding: 0 8px;
  width: 60px;
  margin-left: 10px;
  font-size: 13px;
  font-weight: bold;
  height: 30px;
  border-radius: 5px;
  border: none;
  color: #e0e0e0;
  background-color: #6c6c6c;
  cursor: pointer;
}

@media (max-device-width: 640px) {
  .folderTabWrapper {
    display: none;
  }
  .addFolder {
    padding-top: 1rem;
    padding-left: 1rem;
    background-color: #131313;
    width: 80%;
  }
  .input {
    width: 50%;
  }
  .tag {
    padding-right: 55vw;
  }
}
